*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f7f9;
  color: #333;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

/* -------------------------------------------------- */

/* Main */
body > h1 {
  text-align: center;
  margin-bottom: 40px;
  margin-top: 30px;
  font-size: 3rem;
  color: #2c3e50;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

#main {
  display: flex;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  margin-top: 20px;
}

#main > div {
  flex: 1;
  background-color: transparent;
  border-radius: 12px;

  position: relative;
}

#main > div:nth-child(1) {
  margin-left: 30px;
}
#main > div:nth-child(2) {
  margin-right: 30px;
}

#main > div > h2 {
  margin-bottom: 12px;
  color: #2c3e50;
  font-weight: 600;
  text-align: center;
}

/* board */
.grid {
  display: grid;
  grid-template-columns: 24px repeat(10, 1fr);
  grid-template-rows: 24px repeat(10, 1fr);
  gap: 1px;
  margin: 0 auto;
  position: relative;
}

.cell {
  width: auto;
  height: 32px;
  width: 32px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  border-radius: 2px;
  transition: all 0.2s ease;
}

/* Grid labels */
.row-label,
.col-label {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #2c3e50;
  font-weight: 600;
}

.col-label {
  grid-row: 1;
}

.row-label {
  grid-column: 1;
}

/* board */
#board-container1,
#board-container2 {
  width: 100%;
  max-width: 345px;
  margin: 0 auto;
  border-radius: 4px;
  overflow: visible;
}

/* computer board */
#board-container2:not(.blur) .cell:not(.hit):not(.miss):not(.ship):hover {
  background-color: rgba(128, 128, 128, 0.15);
  cursor: pointer;
  transform: scale(1.05);
}

/* Add improved active/click effect for cells */
#board-container2:not(.blur) .cell:not(.hit):not(.miss):not(.ship):active {
  background-color: rgba(65, 105, 225, 0.3);
  transform: scale(0.95);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  transition: all 0.1s ease;
}

#board-container2.game-over .cell:not(.hit):not(.miss):not(.ship):hover {
  background-color: transparent;
  cursor: default;
  transform: none;
}

/* state */
.ship {
  background-color: rgba(54, 162, 235, 0.6);
}

/* Updated ship cell click effect without scaling */
.cell.ship:active {
  background-color: rgba(54, 162, 235, 0.9);
  border: 2px solid rgba(25, 118, 210, 0.8);
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.25);
  transition: all 0.12s ease;
  position: relative;
}

.hit {
  background-color: rgba(255, 99, 132, 0.8);
  /* Set up animation but keep it paused by default */
  animation: hitEffect 0.8s ease-out;
  animation-play-state: paused;
}

/* Play animation when clicked */
.hit:active {
  animation-play-state: running;
  /* Add pointer cursor to indicate clickability */
  cursor: pointer;
}

/* Make hit cells still look clickable */
.hit:hover {
  border-color: rgba(255, 99, 132, 1);
  cursor: pointer;
}

.miss {
  background-color: rgba(128, 128, 128, 0.3);
}

/* Keep the hitEffect animation */
@keyframes hitEffect {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 99, 132, 0.8);
    border: 1px solid rgba(255, 99, 132, 0.9);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(255, 99, 132, 0);
    border: 2px solid rgba(255, 255, 255, 0.9);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 99, 132, 0);
    border: 1px solid rgba(0, 0, 0, 0.4);
  }
}

/* board blur effect */
#board-container1.blur,
#board-container2.blur {
  opacity: 0.4;
}

/* modal result */
#modal-result {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: absolute;
  background-color: white;
  padding: 30px 60px;
  border-radius: 12px;
  box-shadow:
    0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}

#modal-result > h2 {
  margin-bottom: 20px;
  color: #2c3e50;
  font-size: 1.8rem;
}

#restart-button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 10px 24px;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s ease;
}

#restart-button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

/* random button */
#random {
  margin-top: 20px;
  padding: 12px 24px;
  background-color: #2ecc71;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  display: block;
  margin-left: auto;
  margin-right: auto;
  transition: all 0.2s ease;

  position: absolute;
  left: 40%;
}

#random:hover {
  background-color: #27ae60;
  transform: translateY(-2px);
}

/* game status */
#game-stat-computer {
  position: absolute;
  height: 100px;
  width: 100px;
  right: -8px;
  top: 75px;
  border-radius: 8px;

  opacity: 0.6;
  display: none;
}

#game-stat-real {
  position: absolute;
  height: 100px;
  width: 100px;
  left: -8px;
  top: 75px;
  border-radius: 8px;

  opacity: 0.6;
  display: none;
}

/* ships4 */
#ship-4 {
  margin-bottom: 4px;
}
#ship-4 > .ship-41 {
  display: flex;
  justify-content: end;
}

#ship-4 > .ship-41 > .ship {
